import React from 'react'
import { CartContext } from '../Context/CartContext'
import { useContext } from 'react'
import DeleteIcon from '@mui/icons-material/Delete';
import './CartList.css'
import { NavLink } from 'react-router-dom';

const CartList = () => {
  const { cart, removeCartItem, increaseQuantity, decreaseQuantity ,setCart } = useContext(CartContext)

  const handleDelete = (id) => {
    removeCartItem(id)

  }

  // subtotal 
  const totalPrice = () => {
    let totalAmout = 0;
    cart.forEach(item => {
      totalAmout += item.price * item.quantity;
    })
    return totalAmout;
  }

  const totalAmout = totalPrice();
  const finalTotal = totalAmout-50-5;

  const handlePurchase = () => {
    window.alert('Your order has been placed..Thank you')
    setCart([])
  }

  return (
    <div className='cart-product-list'>
      <h2 style={{ marginLeft: '25px' }}>Shopping Cart</h2>
      <div className="cart-items">

        <div className='h'>
          {
            cart.map((item) => {
              const { id, brand, imageURL
                , price ,quantity ,items_left
              } = item;
              console.log(item)
          
              return (
                <div key={id} className='cart-product'>
                  <img src={imageURL} alt="" />
                  <p>{brand}</p>
                  <p>$ {price}</p>
                  <div className="cart-quantity">
                    <button onClick={() => increaseQuantity(item)}>+</button>
                    <span>{quantity}</span>
                    <button onClick={() => decreaseQuantity(item)}>- </button>
                  </div>
                  <span onClick={() => handleDelete(id)}>
                    <DeleteIcon className='delete-icon' />
                  </span>
                </div>
              )
            })

          }
        </div>
        <div className='order-summary'>
          <h2>Order Summary</h2>
          <div className='sub-total'>
            <span>
              <p>Total Items</p>
              <p>{cart.length <= 9 ? "0" + cart.length : cart.length}</p>
            </span>
            <span>
              <p>Total Price</p>
              <p>{totalAmout}</p>
            </span>
            <span>
              <p>Discount</p>
              <p className='discount-text'>-$ 50</p>
            </span>
            <span>
              <p>Tax</p>
              <p>$ 5</p>
            </span>
            <hr />
            <span>
              <p>Total </p>
              <p className='final-total-text'>$ {finalTotal}</p>
            </span>
            <button className='purchase' onClick={handlePurchase}>Make Purchase</button>
            <NavLink to='/products' className='back-link'>
              <button className='back-shop'>Back to shop</button>
            </NavLink>
          </div>
        </div>


      </div>
    </div>
  )
}

export default CartList
