import { createContext, useState } from "react";


const CartContext = createContext();

const CartProvider = ({ children }) => {

  const [cart, setCart] = useState([]);


  const addToCart = (item) => {
    setCart([...cart, item])

    const existingItem = cart.find((cartItem) => cartItem.id === item.id);

    if (existingItem) {
      // If item already exists in the cart, increase its quantity
      existingItem.quantity += 1;
      setCart([...cart]);
    } else {
      // If item does not exist in the cart, add it with quantity 1
      setCart([...cart, { ...item, quantity: 1 }]);
    }
  }
  const removeCartItem = (id) => {
    setCart(val => val.filter(product => product.id !== id))
  }


  //============

  // increse the cart product quantity 
  const increaseQuantity = (item) => {
    const updatedcart = cart.map((cartItem) => {
      if (cartItem.id === item.id) {
        // if quantity is greater that left itmes then quantity will be items left otherwise increment by one
        let updatedQuantity = cartItem.quantity === 1 && cartItem.quantity <= item.items_left ? cartItem.quantity + 1 : item.items_left;
        return { ...cartItem, quantity: updatedQuantity };
      }
      return cartItem;
    });
    setCart(updatedcart);
  };

  // decrese the cart product quantity
  const decreaseQuantity = (item) => {
    const updatedcart = cart.map((cartItem) => {
      if (cartItem.id === item.id && cartItem.quantity > 1) {
        return { ...cartItem, quantity: cartItem.quantity - 1 };
      }
      return cartItem;
    });
    setCart(updatedcart);
  };




  return (
    <CartContext.Provider value={{ addToCart, cart, removeCartItem, increaseQuantity, decreaseQuantity, setCart }}>
      {children}
    </CartContext.Provider>
  )
}

export { CartContext, CartProvider }