import './Hero.css'
import { useNavigate } from 'react-router-dom'
import shoesTo from '../../components/image/Group 33092.png'
import ArrowRightAltIcon from '@mui/icons-material/ArrowRightAlt';


const Hero = ({myData}) => {
    const navigate = useNavigate();
    return (
        <>
            <div className='hero-section'>
                <div className="hero-content">
                    <h1>Find your <br /> dream snekers</h1> 
                    <button onClick={() => navigate('/products')}>Explore more <ArrowRightAltIcon/> </button>
                </div>
                <div className="hero-img">
                   <img src={shoesTo} alt="" />

                    
                </div>
                <div>
                </div>





            </div>

        </>
    )
}

export default Hero
