import React, { useState } from 'react'
import { NavLink } from 'react-router-dom'
import './Navbar.css'
import { CartContext } from '../Context/CartContext'
import { useContext } from 'react'
import MenuIcon from '@mui/icons-material/Menu';
import ShoppingBagIcon from '@mui/icons-material/ShoppingBag';
import CloseIcon from '@mui/icons-material/Close';
import { SearchTextContext } from '../Context/ProductContext'
import SearchOutlinedIcon from '@mui/icons-material/SearchOutlined';

const Navbar = () => {


 const [searchText, setSearchText] = useContext(SearchTextContext)
  const [menu, setMenu] = useState(false)
  const { cart } = useContext(CartContext)

 
const handleSearch = (e) => {
  let query = e.target.value;
  setSearchText(query)
}
console.log(searchText)

  const handleClickMenu = () => {
    setMenu(!menu)
  }
  return (
    <nav className={menu ? 'nav-bar expand-navbar' : 'nav-bar'}>
   
      <div className={menu ? 'nav-1' : 'nav-1 visible'}>
      <h2>Shop</h2>
        <li>
          <NavLink to='/' className='nav-link'>Home</NavLink>
        </li>
        <li>
          <NavLink to='/about' className='nav-link'>About</NavLink>
        </li>
        <li>
          <NavLink to='/products' className='nav-link'>Products</NavLink>
        </li>
        <li>
          <NavLink to='/contact' className='nav-link'>Contact</NavLink>
        </li>
      </div>
      <div className="nav-2">
        <span className='search-bar'>
          <SearchOutlinedIcon/>
          <input type="search" value={searchText} name="" id="" onChange={handleSearch} placeholder='search by brands' />
        </span>
        <li >
          <span className='cart-link'>
            <NavLink to='cart' className='nav-link'> <ShoppingBagIcon />  </NavLink>
            <p>{cart.length}</p>
          </span>
        </li>
        <li className='toggle-icon' onClick={handleClickMenu}>
          {
            !menu ? <MenuIcon className='menu-icon' /> : <CloseIcon className='menu-icon' />
          }
        </li>

      </div>

    </nav>
  )
}

export default Navbar
