import React from 'react'
import Hero from '../Hero/Hero'
import './Home.css'
import Latest from '../Latest/Latest';
import Service from '../Hero/Service';

const Home = () => {
  return (
    <div className='home'>
      <div className="hero">
        <Hero />
      </div>
      <div className='latest--service'>
        <div className="latest-products">
          <Latest />
        </div>
        <div className='service-of-hero'>
          <Service />
        </div>
      </div>
    </div>
  )
}

export default Home
