import React from 'react'
import PaidOutlinedIcon from '@mui/icons-material/PaidOutlined';
import ContactPhoneOutlinedIcon from '@mui/icons-material/ContactPhoneOutlined';
import LocalShippingOutlinedIcon from '@mui/icons-material/LocalShippingOutlined';
const Service = () => {
    return (
        <div className='hero-service'>
            <div className='hero-payment'>
                <div>
                    <PaidOutlinedIcon className='hero-service-icon' />
                </div>
                <div>
                    <h2>Secure payment</h2>
                    <p>Secure on order</p>
                </div>
            </div>


            <div className='hero-customer'>
                <div>
                    <ContactPhoneOutlinedIcon className='hero-service-icon' />
                </div>
                <div>
                    <h2>Secure payment</h2>
                    <p>Secure on order</p>
                </div>
            </div>

            
            <div className='hero-delivery'>
                <div>
                    < LocalShippingOutlinedIcon className='hero-service-icon' />
                </div>
                <div>
                    <h2>Secure payment</h2>
                    <p>Secure on order</p>
                </div>
            </div>
        </div>
    )
}

export default Service
