import React from 'react'
import SendIcon from '@mui/icons-material/Send';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
import LocationOnOutlinedIcon from '@mui/icons-material/LocationOnOutlined';
import LocalPhoneOutlinedIcon from '@mui/icons-material/LocalPhoneOutlined';
import MailOutlineOutlinedIcon from '@mui/icons-material/MailOutlineOutlined';
import './Contact.css'

const Contact = () => {
  return (
    <>
    <h1 style={{textAlign:'center',marginTop:'60px',marginBottom:'30px'}}>CONTACT US</h1>
    <div className='contact-section'>


<form action="">
  <div>
    <label htmlFor="name">YOUR NAME</label>
    <input type="text" required name="" id="" />
  </div>
  <div>
    <label htmlFor="email">YOUR EMAIL</label>
    <input type="email" required name="" id="" />
  </div>
  <div>
    <label htmlFor="">YOUR MESSEGE</label>
    <input type='text' required className='input-msg' />
  </div>
  <div>
    <button type='submit'>SEND <SendIcon className='send-msg' /> </button>
  </div>
</form>

<div className="contact-others">
  <div className="contact-address">
    <h2>ADDRESS</h2>
    <p><LocationOnOutlinedIcon  className='contact-iconss' />  Jalgaon city , Maharashtra , India , 425001</p>
  </div>
  <div className="contact-phones">
    <h2>PHONES</h2>
    <p> <LocalPhoneOutlinedIcon  className='contact-iconss' />  +91 8830 8949 32</p>
    <p><LocalPhoneOutlinedIcon   className='contact-iconss'/> +91 8830 8949 32</p>
  </div>
  <div className="contact-email">
    <h2>EMAIL</h2>
    <p> <MailOutlineOutlinedIcon  className='contact-iconss' /> gauravchavhan05@gmail.com</p>
  </div>
  <div className="contact-follow">
    <h2>FOLLOW US</h2>
    <span> <LinkedInIcon className='contact-social-icon' /> </span>
    <span> <InstagramIcon className='contact-social-icon' /> </span>
    <span> <TwitterIcon className='contact-social-icon' /> </span>
    <span> <FacebookIcon className='contact-social-icon' />  </span>
  </div>
</div>
</div>
    </>
  )
}

export default Contact
