import { ProductContext, SearchTextContext } from '../Context/ProductContext'
import { useContext } from 'react'
import { NavLink } from 'react-router-dom'
import './Products.css'


const Products = () => {
  const [searchText] = useContext(SearchTextContext)
  const { products } = useContext(ProductContext)
  // const {viewProduct} = useContext(CartContext)

  const searchUpdatedProducts = products.filter((product) =>
    product.brand.toLowerCase().includes(searchText.toLowerCase())
  );
  if (searchUpdatedProducts === true) {
    console.log('yes')
  }
  else {
    console.log('flase')
  }


  return (
    <>

      {/* <Filter/> */}
      <div className='product-list'>

        {searchUpdatedProducts.length > 0 ?
          searchUpdatedProducts.map((item) => {
            const { name, category, id, brand, gender, imageURL, is_in_inventory, items_left
              , price, slug
            } = item;

            return (
              <div key={id} className='product'>
                <NavLink to={`/singleproduct/${id}`}>

                  <img src={imageURL} alt={name} className='product-img' />
                </NavLink>
                <p className='brand'>{brand}</p>
                <p>{name}</p>
                <p className='price'>$ {price}</p>
                {/* <p style={{ textTransform: 'capitalize' }} className='slug'>{slug}</p> */}

              </div>


            )
          }) :
          <div className='product-not-found'>
            <h1>NO PRODUCT FOUND 😔</h1>

          </div>
        }
      </div>

    </>
  )
}

export default Products
