import React from 'react'
import ProductData from '../ProductData'
import { NavLink } from 'react-router-dom'
import './Recomand.css'

const Recomand = () => {
    const recomand = ProductData.filter(item => item.recomanded === true)

    return (
        <div className='recomanded-product-list'>
            {
                recomand.map((item) => {
                    const { name, id, brand, imageURL,

                    } = item;
                    return (
                        <NavLink to={`/singleproduct/${id}`} className='nav-link  ' key={id}>

                            <div className='recomand-product'>
                               
                                <img src={imageURL} alt="" />
                               
                            </div>

                        </NavLink>
                    )
                })
            }
        </div>
    )
}

export default Recomand
