import { NavLink } from 'react-router-dom'
import './Footer.css'
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
const Footer = () => {
  return (
    <footer>
      <div className='footer-links'>
        <p>useful links</p>

        <NavLink className='nav-link-footer' to='/about'>About</NavLink>
        <NavLink className='nav-link-footer' to='/products' >Products</NavLink>
        <NavLink className='nav-link-footer' to='/contact' >Contact</NavLink>
        <NavLink className='nav-link-footer' to='/cart' >Cart</NavLink>


      </div>

      <div className='footer-subscribe'>
        <p>get in touch</p>
        <input type="email" placeholder='example05@gmail.com' required />
        <button>Subscribe</button>
      </div>

      <div className='footer-contact'>
        <p>contact us</p>
        <span>
          123,Shivam appartment,3rd floor,jalgaon ,<br />maharashtra 425001
        </span>
        <div className='social-footer'>
          <span>
            <LinkedInIcon />
          </span>
          <span>
            <FacebookIcon />
          </span>
          <span>
            <InstagramIcon />
          </span>
          <span>
            <TwitterIcon />
          </span>
        </div>
      </div>

    </footer>
  )
}

export default Footer
